// показать меню на мобильном
$('.touch-nav').on('click', function(e){
  e.preventDefault();
  $('.touch-nav').fadeOut();
  $('.nav-container').fadeIn();
});

// скрыть меню на мобильном
$('.nav-close').on('click', function(e){
  e.preventDefault();
  $('.touch-nav').fadeIn();
  $('.nav-container').fadeOut();
});

