function resizePage() {
  var windowWidth = $(window).outerWidth(); 
	// если окно с текстом открыто и меняется положение тач устройства, проверяем высоту и выставляем необходимые параметры
	if($('.about-popup').hasClass('is-active')) {
		aboutPopupHeight();
	}

  /*start 11646*/

  if(windowWidth >= 768) {
    if($('.brandSlider') || $('.courseSlider')) {
      if((!$('.brandSlider').hasClass('sliderLoaded')) || (!$('.courseSlider').hasClass('sliderLoaded'))) {
        sliderInit()
      }
    }



  }
  if(windowWidth <= 767) {    
    if($('.brandSlider') || $('.courseSlider')) {
      if(($('.brandSlider').hasClass('sliderLoaded')) || ($('.courseSlider').hasClass('sliderLoaded'))) {

        sliderNames.map(function(slider) {
          sliderDestroy(slider)    
          $('.'+slider).removeClass('sliderLoaded').addClass('sliderLoading');
        });

      }
    }  

  }  
  /*end 11646*/

}//end resizePage
window.addEventListener("resize", resizePage);


function loadPage() {
  var top = (document.documentElement && document.documentElement.scrollTop) || document.body.scrollTop;
  if (top > 100) { $('.header__top').addClass('headerFix'); }

  /*start 11646*/
    $('.brandSlider') || $('.courseSlider') ? sliderInit() : null;
  /*end 11646*/

  

}//end loadPage
window.addEventListener("load", loadPage);




function scrollTo(to) {
	var $obj = jQuery('html, body');
	var top = 0;
	var speed = 500;
	var offsetX = 50;

	if (typeof to == 'object') {
		top = to.offset().top;
	} else if (typeof to == 'string') {
		top = jQuery(to).offset().top;
	} else if (typeof to == 'number') {
		top = to;
	}

	if (arguments.length > 1) {
		if (typeof arguments[0] == 'number' && typeof arguments[1] == 'number') {
			speed = arguments[1];
		} else if (typeof arguments[1] == 'string' || typeof arguments[1] == 'object') {
			if (typeof arguments[1] == 'object') {
				$obj = arguments[1];
			} else if (typeof arguments[1] == 'string') {
				$obj = jQuery(arguments[1]);
			}
		}
		if (typeof arguments[2] == 'number') {
			speed = arguments[2];
		}
	}

	if (jQuery(window).width() < 1000) {
		offsetX = 130;
	}

	if (speed == 0) {
		speed = 1;
	}

	$obj.animate(
		{
			scrollTop: top - offsetX
		},
		speed
	);
}

// видео 
function findVideos() {
  let videos = document.querySelectorAll('.video__box');

  for (let i = 0; i < videos.length; i++) {
    setupVideo(videos[i]);
  }
}

function setupVideo(video) {
  let link = video.querySelector('.video__link');
  let media = video.querySelector('.video__media');
  let button = video.querySelector('.video__button');
  let id = parseMediaURL(media);

  video.addEventListener('click', () => {
    let iframe = createIframe(id);

    link.remove();
    button.remove();
    video.appendChild(iframe);
  });

  link.removeAttribute('href');
  video.classList.add('video--enabled');
}

function parseMediaURL(media) {
  let regexp = /https:\/\/i\.ytimg\.com\/vi\/([a-zA-Z0-9_-]+)\/maxresdefault\.jpg/i;
  let url = media.src;
  let match = url.match(regexp);

  return match[1];
}

function createIframe(id) {
  let iframe = document.createElement('iframe');

  iframe.setAttribute('allowfullscreen', '');
  iframe.setAttribute('allow', 'autoplay');
  iframe.setAttribute('src', generateURL(id));
  iframe.classList.add('video__media');

  return iframe;
}

function generateURL(id) {
  let query = '?rel=0&showinfo=0&autoplay=1';

  return 'https://www.youtube.com/embed/' + id + query;
}

findVideos();